.popup__container {
  box-sizing: border-box;
  margin: 0;
  padding: 34px 36px 36px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, .15);
  border-radius: 10px;
  background-color: #fff;
  position: relative;
}

@media screen and (max-width: 620px) {
  .popup__container {
    padding: 30px 20px;
  }
}
